.wrapper {
  width: 300px;
  padding: 50px 110px 50px 0;
  border-right: 1px solid rgba(255, 255, 255, 0.4);
  margin-top: -70px;
}

.button {
  width: 110px;
  text-decoration: none;
  background-color: inherit;
  color: #ffffff;
  padding: 10px;
  transition: 0.2s;
  border: 1px solid #ffffff;
  border-radius: 10px;
  text-align: center;
  font-size: 16px;
  margin-top: 15px;
  cursor: pointer;
}

.button:hover {
  color: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.4);
  transition: 0.2s;
}

.input {
  margin-bottom: 25px;
}
